.box-preloader {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  z-index: 99999;
  background-color: aquamarine;
}
.box-preloader .preloader-logo,
.box-preloader .preloader-logo2 {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 100px;
  height: 100px;
}

.spinner {
  border: 5px solid transparent;
  border-top: 5px solid black;
  border-bottom: 5px solid black;
  border-radius: 50%;
  width: 150px;
  height: 150px;
  animation: spin 1s ease-in-out infinite;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.preloader-logo {
  -webkit-animation: spinner 1.5s infinite linear;
  animation: spinner 1.5s infinite linear;
}

@-webkit-keyframes spinner {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes spinner {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}